.Apod {
    .picture {
        background-position: 50% 50%;
        background-size: cover;
        transition: opacity 0.25s ease-out; }

    .title {
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom: 1rem;
        left: 1rem;
        right: 1rem;

        p {
            margin: 4px 0 0; } } }
